.full-card{
    z-index:10;
    top:0;
    right:0;
    bottom:0;
    left:0;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.7);
}

.full-card__inner-wrap{
    padding-top:100px;
    max-width:800px;
    padding-right:100px;
    padding-left:100px;
    margin:0 auto;
}

.full-card__description{
    display: block;
    resize: none;
    width:calc(100% - 20px);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-right:3px;
    border-color: transparent;
    background-color: transparent;
    -webkit-transition: backgound-color 200ms, -webkit-box-shadow 200ms;
    transition: backgound-color 200ms, -webkit-box-shadow 200ms;
    -o-transition: backgound-color 200ms, box-shadow 200ms;
    transition: backgound-color 200ms, box-shadow 200ms;
    transition: backgound-color 200ms, box-shadow 200ms, -webkit-box-shadow 200ms;
}

.full-card__description:focus{
    padding:10px;
    background-color: rgba(0,0,0,0.05);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
            box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    outline: none;
 }

.full-card__inner{
    position: relative;
    padding-top:20px;
    padding-right:20px;
    padding-left:20px;
    padding-bottom:30px;
    border-radius: 3px;
    background-color: rgb(240, 240, 240);
}

.full-card__btn-close{
    position: absolute;
    right:20px;
    top: 20px
}

.full-card__info{
    display: -ms-flexbox;
    display: flex;
    font-size:11px;
    text-transform: uppercase;
    opacity: 0.6;
    font-weight:bold;
    margin-bottom:25px;
}


.full-card__id{
    margin:0;
    margin-right:30px;
}

.full-card__date{
    margin:0;
}


.full-card__body{
    display: -ms-flexbox;
    display: flex;
}

.full-card__description-column{
    width:400px;
    -ms-flex-positive: 10;
        flex-grow: 10;
}
.card-side-column{
    -ms-flex-preferred-size:200px;
        flex-basis:200px;
    -ms-flex-positive: 0;
        flex-grow: 0;
}

.full-card__title{

    width:100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    resize: none;
    font-size:18px;
    font-weight:bold;
    border:none;
    margin-bottom:15px;
    background-color: transparent;
    border-radius: 3px;
    -webkit-transition: background-color 400ms;
    -o-transition: background-color 400ms;
    transition: background-color 400ms;
}

.full-card__title:focus{
    background-color: #fff;
    outline:none;
}

.full-card__title-wrapper{
    padding-right:50px;
}


