.input{
    width: 100%;
    border-right: 3px;
    border: none;
    border-radius: 3px;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.input--bordered{
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f9f9f9;
}


