.dragged-column-wrap{
    position: fixed;
    -webkit-transition: -webkit-transform 300ms;
    transition: -webkit-transform 300ms;
    -o-transition: transform 300ms;
    transition: transform 300ms;
    transition: transform 300ms, -webkit-transform 300ms;
    /*transition: transform 300ms, top 100ms linear, left 100ms linear;*/
    /*transform: rotateZ(0);*/
    -webkit-transform-origin:50% 0 ;
        -ms-transform-origin:50% 0 ;
            transform-origin:50% 0 ;
    -webkit-transform: rotateZ(-3deg);
        -ms-transform: rotate(-3deg);
            transform: rotateZ(-3deg);
    will-change: top, left;
}

.dragged-column-wrap--activate-transition{
    -webkit-transition: top 80ms linear, left 80ms linear, -webkit-transform 300ms;
    transition: top 80ms linear, left 80ms linear, -webkit-transform 300ms;
    -o-transition: transform 300ms, top 80ms linear, left 80ms linear;
    transition: transform 300ms, top 80ms linear, left 80ms linear;
    transition: transform 300ms, top 80ms linear, left 80ms linear, -webkit-transform 300ms;
}

