.add-column-form {
    width: 300px;
    vertical-align: top;
    display: inline-block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

*+.add-column-form{
    margin-left:5px;
}

.add-column-form.add-column-form--showed {
    background-color: rgba(255, 255, 255, 0.7);
    padding: 10px;
    border-radius: 2px;
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.add-column-form__inner {
    display: none;
}

.add-column-form--showed .add-column-form__inner {
    display: block;
}

.add-column-form__placeholder {
    display: block;
    padding: 10px;
    border-radius: 2px;
    -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.4);
            box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.4);
    background-color: rgba(0, 0, 0, 0.25);
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.6);
    color:#fff;
    -webkit-transition: background-color 300ms, -webkit-box-shadow 300ms;
    transition: background-color 300ms, -webkit-box-shadow 300ms;
    -o-transition: background-color 300ms, box-shadow 300ms;
    transition: background-color 300ms, box-shadow 300ms;
    transition: background-color 300ms, box-shadow 300ms, -webkit-box-shadow 300ms;
}

.add-column-form--showed .add-column-form__placeholder {
    display: none;
}

.add-column-form__placeholder:hover {
    -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3);
            box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3);
    background-color: rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.add-column-form__input {
    margin-bottom: 5px;
}



.add-column-form_btn + .add-column-form_btn{
    margin-left:5px;
}

.add-column-form_btn{
    /*margin-left:5px;*/
}




